.settings-btn {
    position:absolute;
    background-color: transparent;
    border: 0;
    top: 20px;
    right: 20px;
    color: #777777;
}

.settings-btn:hover {
    cursor: pointer;
}

button:focus {
    outline: 0;
}

.login {
  background-position: center;
  background-size: cover;
  display: flex;
  flex: 1;
  -webkit-animation: slide 6s linear 1;
}

.kakao-login-btn {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  margin-top: 300px;
}

@-webkit-keyframes slide {
  from {background-position: -330px 0;}
  to {background-position: 100% 0;}
}
